  position: fixed;
  top: -60px;
  left: 0;
  z-index: 100;
  background-color: #ececec;
  width: 100%;
  height: 60px;
  cursor: pointer;
  text-align: center;
  overflow: hidden;
  transition: all 0.5s linear;
}

.wakeup-app .close {
  float: left;
  width: 40px;
  height: 60px;
  position: relative;
  cursor: pointer;
}

.wakeup-app .close:after {
  position: absolute;
  display: block;
  content: "\D7";
  width: 18px;
  height: 18px;
  font-size: 14px;
  line-height: 16px;
  border-radius: 9px;
  color: #fff;
  background-color: #dddedf;
  top: 50%;
  left: 50%;
  margin-top: -9px;
  margin-left: -9px;
}

.wakeup-app .app-icon {
  float: left;
  width: 60px;
  height: 60px;
  background-image: url("/v2/image/wap/weixin_share_default_logo.png");
  background-size: 80% 80%;
  background-repeat: no-repeat;
  background-position: 50%;
}

.wakeup-app .description {
  float: left;
  text-align: left;
  padding: 5px 0 5px 3px;
}

.wakeup-app .description .title {
  margin: 6px 0;
  font-size: 15px;
  line-height: 18px;
  font-weight: 400;
}

.wakeup-app .description .meta {
  color: #999;
  line-height: 1;
  font-size: 12px;
  letter-spacing: 1px;
}

.wakeup-app .btn {
  float: right;
  margin-top: 12px;
  margin-right: 12px;
  padding: 8px;
  border-color: #fc5959;
  background-color: #fc5959;
  letter-spacing: 1px;
}

.wakeup-app-mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: #000;
  opacity: 0.8;
  z-index: 101;
}

.wakeup-app-opentip {
  position: fixed;
  height: 144px;
  top: 0;
  left: 10px;
  right: 10px;
  z-index: 102;
}

.wakeup-app-opentip .lay {
  width: 100%;
  height: 120px;
  background-color: #f9f9f9;
  border-radius: 0 0 8px 8px;
  background: #f9f9f9 url("/v2/image/wap/opentip.png") 100% 4px no-repeat;
  background-size: 127px auto;
}

.wakeup-app-opentip .note {
  float: right;
  margin: 39px 58px 0 0;
  width: 160px;
  line-height: 20px;
}

.wakeup-app-opentip .note h3 {
  font-size: 20px;
  margin-bottom: 6px;
  font-weight: 400;
}

.wakeup-app-opentip .note p {
  font-size: 14px;
}

.bottom-nav {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 0;
  background-color: #fff;
  z-index: 999;
  -o-border-image: url("https://b.yzcdn.cn/v2/image/wap/border-line-2.png") 2
    stretch;
  border-image: url("https://b.yzcdn.cn/v2/image/wap/border-line-2.png") 2
    stretch;
  border-top: 2px solid #e5e5e5;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  .bottom-nav {
    border-top-width: 1px;
  }
}

.bottom-nav ul {
  position: relative;
  height: 100%;
  zoom: 1;
}

.bottom-nav ul:after {
  content: "";
  display: table;
  clear: both;
}

.bottom-nav li {
  display: block;
  float: left;
  width: 25%;
  height: 100%;
}

.bottom-nav a {
  display: block;
  width: 100%;
  height: 100%;
  padding-top: 8px;
  text-align: center;
  font-size: 10px;
}

.bottom-nav [class^="icon-"] {
  display: block;
  width: 22px;
  height: 22px;
  margin: auto;
  margin-bottom: 4px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.bottom-nav .icon-home {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .icon-category {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .icon-cart {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .icon-user {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .icon-sign {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .active a {
