}

.main-content .category-loading {
  margin-left: -21px;
  margin-top: 120px;
  left: 50%;
  border-width: 3px;
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  border-style: solid;
  border-color: #d9d9d9 #d9d9d9 #d9d9d9 transparent;
  box-shadow: 0 0 0 1px transparent;
  animation: loader 0.8s linear;
  animation-iteration-count: infinite;
}

.main-content .category-tip {
  margin-top: 120px;
  font-size: 12px;
  color: #ccc;
  left: 50%;
